function App() {
  const [cameraPosition, setCameraPosition] = useState([0, 5, -15]);

  const [nodes, setNodes] = useState([
    { id: 1, position: [-2, 1, 3], color: "red", size: 0.5, panel: null },
    { id: 2, position: [3, 2, 0], color: "red", size: 0.5, panel: null },
    { id: 3, position: [0, 3, -3], color: "red", size: 0.5, panel: null },

    { id: 4, position: [3.5, 1, 0], color: "green", size: 0.3, panel: null },
    { id: 5, position: [4, 1.5, 1], color: "green", size: 0.3, panel: null },
    { id: 6, position: [-3, 2, 3], color: "green", size: 0.3, panel: null },

    { id: 7, position: [4, 2, 3], color: "blue", size: 0.1, panel: null },
  ]);
  const [edges, setEdges] = useState([
    { start: 1, end: 2 },
    { start: 1, end: 3 },
    { start: 2, end: 3 },

    { start: 1, end: 6 },
    { start: 2, end: 4 },
    { start: 2, end: 5 },

    { start: 5, end: 7 },
  ]);
  const [planes, setPlanes] = useState([{ position: [0, 0, 0], size: [4, 1] }]);

  const getNodeById = (id) => nodes.find((node) => node.id === id);

  const updateNodes = (updates) => {
    setNodes(
      nodes.map((node) =>
        node.id in updates ? { ...node, ...updates[node.id] } : node
      )
    );
  };

  const setSceneHome = () => {
    updateNodes({
      1: { position: [-2, 1, 3] },
      2: { position: [3, 2, 0] },
      3: { position: [0, 3, -3] },
      4: { position: [3.5, 1, 0] },
      5: { position: [4, 1.5, 1] },
      6: { position: [-3, 2, 3] },
      7: { position: [4, 2, 3] },
    });
    setCameraPosition([0, 5, -15]);
  };

  const setSceneList = () => {
    updateNodes({
      1: { position: [3, 4, -4] },
      2: { position: [3, 2.5, -4] },
      3: { position: [3, 1, -4] },
      4: { position: [4.2, 2, 0] },
      5: { position: [4.1, 3.2, 0] },
      6: { position: [4.1, 5.5, 0] },
      7: { position: [4.5, 4, 1] },
    });
    setCameraPosition([3, 1.5, -12]);
  };

  return (
    <div className="App">
      <VRButton />
      <Canvas
        shadows
        camera={{
          position: cameraPosition,
          rotation: [0, 0, 0],
          fov: 35,
          near: 1,
          far: 40,
        }}
      >
        <XR>
          <Controllers />
          <Hands />

          <ambientLight intensity={0.5} />
          <color attach="background" args={["#000000"]} />
          <spotLight intensity={100} position={[5, 5, -5]} castShadow />

          <CameraAnimation position={cameraPosition} />

          {nodes.map((node, index) => (
            <Node
              key={node.id}
              id={node.id}
              position={node.position}
              color={node.color}
              size={node.size}
            />
          ))}
          {edges.map((edge, index) => {
            const nodeStart = getNodeById(edge.start);
            const nodeEnd = getNodeById(edge.end);
            return (
              <Line
                key={index}
                start={nodeStart.position}
                end={nodeEnd.position}
              />
            );
          })}
          {planes.map((plane, index) => (
            <PlaneComponent
              key={index}
              position={plane.position}
              size={plane.size}
            />
          ))}

          {/* <EffectComposer>
          <DepthOfField focusDistance={0.1} focalLength={0.1} bokehScale={0.5} />
        </EffectComposer> */}

          <gridHelper args={[10, 10]} />
          <OrbitControls target={[0, 3, 0]} />
          <axesHelper />
          <Stats />
        </XR>
      </Canvas>

      <div id="menu">
        <button onClick={() => setSceneHome()}>Home</button>
        <button onClick={() => setSceneList()}>List</button>
        <button onClick={() => {}}>Detail</button>
        <button onClick={() => {}}>Combine</button>
      </div>
    </div>
  );
}

export default App;
